import React, {Component} from 'react';
import styles from '../sass/components/popUp.scss';

class PopUp extends Component {
  render() {
    return (
      <div className={styles.popUp} onClick={() => this.props.popUp()}>
        <div>
          <span onClick={() => this.props.popUp()}>&times;</span>
          <h1>
            Tu universidad no tiene acceso a Booklick aun.
          </h1>
          <a target={'_blank'} href={'https://booklick.typeform.com/to/kHar0d'} className={styles.button}>Solicita Booklick para tu Universidad</a>
        </div>
      </div>
    );
  }
}

export default PopUp;
